<template>
<div id="mainContainer"><span type="bbsrow" srow="0"><div><span class="" data-type="bbsline" data-row="0"><span><span class="q15 b4">                    </span><span class="q11 b4">BBS</span><span class="q15 b4">                                  </span><span class="q7 b0"> </span></span></span><div></div></div></span><span type="bbsrow" srow="1"><div><span class="" data-type="bbsline" data-row="1"><span><span class="q7 b0">[][q] [][r] [] [PgUp][PgDn] [c] [/] [h] </span></span></span><div></div></div></span><span type="bbsrow" srow="2"><div><span class="" data-type="bbsline" data-row="2"><span><span class="q0 b7">                                                   </span><span class="q7 b0"> </span></span></span><div></div></div></span><span type="bbsrow" srow="3"><div><span class="" data-type="bbsline" data-row="3"><span><span class="q7 b0">{{ getPointer(0) }}</span><span class="q7 b0">1   DigitalLife  </span><span class="q11 b0"> </span><span class="q7 b0">                      10 </span></span></span><div></div></div></span><span type="bbsrow" srow="4"><div><span class="" data-type="bbsline" data-row="4"><span><span class="q7 b0">{{ getPointer(1) }}</span><span class="q7 b0">2   System       </span><span class="q12 b0"> </span><span class="q7 b0">                                      </span></span></span><div></div></div></span><span type="bbsrow" srow="5"><div><span class="" data-type="bbsline" data-row="5"><span><span class="q7 b0">{{ getPointer(2) }}</span><span class="q7 b0">3   Z_Archived   </span><span class="q10 b0"> </span><span class="q7 b0">                                    </span></span></span><div></div></div></span><span type="bbsrow" srow="6"><div><span class="" data-type="bbsline" data-row="6"><span><span class="q7 b0">{{ getPointer(3) }}</span><span class="q7 b0">4   DigitalTech  </span><span class="q15 b0"> </span><span class="q7 b0">                         </span></span></span><div></div></div></span><span type="bbsrow" srow="7"><div><span clas