pan></span></span><div></div></div></span><span type="bbsrow" srow="14"><div><span class="" data-type="bbsline" data-row="14"><span><span class="q7 b0">{{ getPointer(11) }}</span><span class="q7 b0">12   G_RolePlay   </span><span class="q15 b0"> </span><span class="q7 b0">                         </span></span></span><div></div></div></span><span type="bbsrow" srow="15"><div><span class="" data-type="bbsline" data-row="15"><span><span class="q7 b0">{{ getPointer(12) }}</span><span class="q7 b0">13   G_SportAct   </span><span class="q15 b0"> </span><span class="q7 b0">                         </span></span></span><div></div></div></span><span type="bbsrow" srow="16"><div><span class="" data-type="bbsline" data-row="16"><span><span class="q7 b0">{{ getPointer(13) }}</span><span class="q7 b0">14   G_Strategy   </span><span class="q15 b0"> </span><span class="q7 b0">                         </span></span></span><div></div></div></span><span type="bbsrow" srow="17"><div><span class="" data-type="bbsline" data-row="17"><span><span class="q7 b0">{{ getPointer(14) }}</span><span class="q7 b0">15   GameTopics   </span><span class="q10 b0"> </span><span class="q7 b0">                       2 </span></span></span><div></div></div></span><span type="bbsrow" srow="18"><div><span class="" data-type="bbsline" data-row="18"><span><span class="q7 b0">                                                                                </span></span></span><div></div></div></span><span type="bbsrow" srow="19"><div><span class="" data-type="bbsline" data-row="19"><span><span class="q7 b0">                                                                                </span></span></span><div></div></div></span><span type="bbsrow" srow="20"><div><span class="" data-type="bbsline" data-row="20"><span><span class="q7 b0">                                                                                </span></span></span><div></div></div></span><span type="bbsrow" srow="21"><div><span class=""